<script>
import Tabulator from 'tabulator-tables'
export default { 
    name: 'beam-table',
    props: {
        beam: {
            type: Object,
            required: true

        }

    },
    data() {
        return {
            formwork: {
                labour: {}

            },
            rebar: {
                labour: {}

            },
            concrete: {
                additives:{},
                cement: {
                    volume: null,
                    bags: null
                },
                sand: null,
                stone: null,
                water: null,
                labour: {}

            },
            labour: {}

        }
    },
    mounted() {
        
    },
    computed: {
        processFormwork() {
            const self = this;
            const side = {
                material: {
                    material: "5/8 PlyBoard",
                    unit: 'sheets',
                    area: 32.00
                },
                depth: self.beam.depth + 0.15
            }
            if(beam.unit == 'metric'){
                side.length= 8.00 / 3.124 // meter
                side.material.area = side.material.area / 10.876 // m2
            }
            else { side.length = 8.0 } // rnft 

            side.amount = (self.beam.length / side.length ) * 2
            side.area = side.depth * side.length * side.amount
            side.material.amount = side.area / side.material.area

            self.formwork.sides = side

            return void 0            

        },
        processConcrete() {
            

            const self = this, ratio = [], mixratio = self.beam.concrete.ratio.split(':')   
            //self.processFormwork    
            for(let i of mixratio){ ratio.push( parseInt(i) ) }
            //console.log(ratio)
            var sum_ratio = ratio.reduce(function(a,b){return a + b;}, 0);
            this.concrete.cement.volume = (self.beam.concrete.volume * ( ratio[0] / sum_ratio )).toFixed(3)
            this.concrete.sand = (self.beam.concrete.volume * ( ratio[1] / sum_ratio )).toFixed(3)
            this.concrete.stone = (self.beam.concrete.volume * ( ratio[2] / sum_ratio )).toFixed(3)
            //console.log(sum_ratio)          
        
            return ratio
        }
    },
    methods: {
        
        
    }
}
</script>
